import { caiNoFinalDeFevereiro } from './caiNoFinalDeFevereiro';
import { validarData } from '../validacao';
import { METODO_DIAS_360 } from '../enums';

/**
 * Equivalente à função DIAS360 do Excel.
 * Uma vez que o Excel implementa o método "US/NASD" incorretamente, uma opção "EXCEL" foi criada para fins de compatibilidade.
 * Um resumo, o Excel omite um passo da aplicação do método `US-NASD` consistente em alterar a data final para o dia 30 do mês se tanto a data inicial quanto a final caem no último dia de fevereiro.
 *
 * Informações úteis:
 * - [https://wiki.openoffice.org/wiki/Documentation/How_Tos/Calc:_Date_&_Time_functions#Financial_date_systems](https://wiki.openoffice.org/wiki/Documentation/How_Tos/Calc:_Date_&_Time_functions#Financial_date_systems)
 * - [https://github.com/spacecraftinc/days360/blob/master/index.js](https://github.com/spacecraftinc/days360/blob/master/index.js)
 *
 * Uso:
 * ```
 * console.log(dias360(new Date('2024-02-29T00:00'), new Date('2030-01-31T00:00'), METODO_DIAS_360.EXCEL)));
 * // Saída: 2130
 * ```
 *
 * @param inicio
 * @param fim
 * @param metodo Se omitido, o método `METODO_DIAS_360.EXCEL` (i.e `METODO_DIAS_360.USD_NASD` modificado)
 */
export function dias360(inicio: Date, fim: Date, metodo: METODO_DIAS_360 = METODO_DIAS_360.EXCEL): number {
  if (!validarData(inicio) || !validarData(fim)) throw new Error('A função aceita somente datas válidas!');

  let diaInicial = inicio.getDate();
  const mesInicial = inicio.getMonth() + 1;
  const anoInicial = inicio.getFullYear();

  let diaFinal = fim.getDate();
  const mesFinal = fim.getMonth() + 1;
  const anoFinal = fim.getFullYear();

  if (metodo === METODO_DIAS_360.EU) {
    diaInicial = Math.min(diaInicial, 30);
    diaFinal = Math.min(diaFinal, 30);
  } else {
    if (metodo === METODO_DIAS_360.US_NASD && caiNoFinalDeFevereiro(inicio) && caiNoFinalDeFevereiro(fim)) {
      diaFinal = 30;
    }
    if (caiNoFinalDeFevereiro(inicio) || diaInicial === 31) {
      diaInicial = 30;
    }
    if (diaInicial === 30 && diaFinal === 31) {
      diaFinal = 30;
    }
  }

  return (anoFinal - anoInicial) * 360 + (mesFinal - mesInicial) * 30 + (diaFinal - diaInicial);
}
