import type { IResultadoAnalisador } from '../comuns'
import type { ITabelasTc } from './tipos'

export default function gerarOpcoes(nome: string, tabelas: ITabelasTc): IResultadoAnalisador {
  return {
    nome: nome,
    resumo: tabelas.resumo,
    opcoes: [
      {
        nome: 'Períodos',
        parametros: [
          {
            intervalo: 'TCInformado',
            dados: tabelas.periodos
          }
        ]
      },
      {
        nome: 'Nova Página',
        parametros: [
          {
            intervalo: '',
            dados: tabelas.novaPagina
          }
        ]
      }
    ]
  }
}
