import { diferencaEntreDatas } from './diferencaEntreDatas';
import { validarData } from '../validacao';
import { TIPO_DURACAO } from '../enums';

const validatePeriod = (start: Date, end: Date) => {
  if (!validarData(start) || !validarData(end)) throw new Error('A função aceita somente datas válidas!');
  if (end.valueOf() < start.valueOf()) throw new Error('Período inválido!');
};

/**
 * Retorna o número de dias sobrepostos considerando dois intervalos de tempo. Os intervalos de tempo corresponde ao tempo entre duas datas, considerando apenas dia, mês e ano.
 *
 * Uso:
 * ```
 * console.log(diasSobrepostos(new Date('2020-01-01'), new Date('2020-05-30'), new Date('2020-05-15'), new Date('2020-07-30')))
 * Saída: 16
 * ```
 *
 * @param inicioPrimeiro Start date of the first interval
 * @param fimPrimeiro End date of the first interval
 * @param inicioSegundo Start date of the second interval
 * @param fimSegundo End date of the second interval
 */
export function diasSobrepostos(
  inicioPrimeiro: Date,
  fimPrimeiro: Date,
  inicioSegundo: Date,
  fimSegundo: Date
): number {
  validatePeriod(inicioPrimeiro, fimPrimeiro);
  validatePeriod(inicioSegundo, fimSegundo);
  if (inicioSegundo.valueOf() > fimPrimeiro.valueOf() || inicioPrimeiro.valueOf() > fimSegundo.valueOf()) return 0;
  const initial = Math.max(inicioPrimeiro.valueOf(), inicioSegundo.valueOf());
  const final = Math.min(fimPrimeiro.valueOf(), fimSegundo.valueOf());

  return diferencaEntreDatas(new Date(initial), new Date(final), TIPO_DURACAO.DIAS) + 1;
}
