import { tipoNumero } from '../predicados';
import { validarData } from '../validacao';

/**
 * Retorna o primeiro dia do mês, permitindo adição ou subtração de meses.
 *
 * Uso:
 * ```
 * console.log(primeiroDiaDoMes(new Date(2020, 0, 31)));
 * // Saída: Wed Jan 01 2020 00:00:00 GMT-0000 (UTC)
 * ```
 *
 * @param data
 * @param mesesParaAdicionar
 */
export function primeiroDiaDoMes(data: Date, mesesParaAdicionar?: number): Date {
  if (!validarData(data)) throw new Error('A função aceita somente datas válidas!');
  mesesParaAdicionar = tipoNumero(mesesParaAdicionar) ? mesesParaAdicionar : 0;
  return new Date(data.getFullYear(), data.getMonth() + mesesParaAdicionar, 1);
}
