/**
 * Verifica se uma data corresponde ao última dia de fevereiro.
 *
 * @param data
 */
export function caiNoFinalDeFevereiro(data: Date): boolean {
  const clone = new Date(data.getTime());
  clone.setMonth(2);
  clone.setDate(0);
  return data.getMonth() === clone.getMonth() && data.getDate() === clone.getDate();
}
