import { normalizar } from './normalizar';

/**
 * Formata uma string no padrão `snake_case`.
 * Lança um erro se o parâmetro de entrada não é uma string (pelas outras funções invocadas).
 *
 * @param str
 */
export function formatoSnakeCase(str: string): string {
  return normalizar(str).replace(/[\s]+/g, '_').toLowerCase();
}
