import { validarData } from '../validacao';
import { TIPO_DURACAO } from '../enums';

const deMiliParaSegundos = (milisegundos: number): number => milisegundos / 1000;
const deMiliParaMinutos = (milisegundos: number): number => deMiliParaSegundos(milisegundos) / 60;
const deMiliParaHoras = (milisegundos: number): number => deMiliParaMinutos(milisegundos) / 60;
const deMiliParaDias = (milisegundos: number): number => deMiliParaHoras(milisegundos) / 24;

/**
 * Equivalente à função `DATEDIFF` do Excel.
 *
 * Uso:
 * ```
 * console.log(dateDiff(new Date('2020-01-01T00:00'), new Date('2021-01-01T00:00'), TIPO_DURACAO.ANOS)));
 * // Saída: 1
 * ```
 *
 * @param inicio
 * @param fim
 * @param tipoDuracao
 */
export function diferencaEntreDatas(
  inicio: Date,
  fim: Date,
  tipoDuracao: TIPO_DURACAO = TIPO_DURACAO.SEGUNDOS
): number {
  if (!validarData(inicio) || !validarData(fim)) throw new Error('A função aceita somente datas válidas!');
  switch (tipoDuracao) {
    case TIPO_DURACAO.SEGUNDOS:
      return Math.ceil(deMiliParaSegundos(fim.valueOf()) - deMiliParaSegundos(inicio.valueOf()));
    case TIPO_DURACAO.MINUTOS:
      return Math.ceil(deMiliParaMinutos(fim.valueOf()) - deMiliParaMinutos(inicio.valueOf()));
    case TIPO_DURACAO.HORAS:
      return Math.ceil(deMiliParaHoras(fim.valueOf()) - deMiliParaHoras(inicio.valueOf()));
    case TIPO_DURACAO.DIAS:
      return Math.ceil(deMiliParaDias(fim.valueOf()) - deMiliParaDias(inicio.valueOf()));
    case TIPO_DURACAO.MESES:
      return fim.getUTCMonth() - inicio.getUTCMonth() + (fim.getUTCFullYear() - inicio.getUTCFullYear()) * 12;
    case TIPO_DURACAO.ANOS:
      return fim.getUTCFullYear() - inicio.getUTCFullYear();
    default:
      throw new Error('Parâmetro de duração inválido!');
  }
}
