import type { IOpcaoAnalisador, IResultadoAnalisador } from '../comuns'
import type { ITabelasCnis } from './tipos'

export default function gerarOpcoes(nome: string, tabelas: ITabelasCnis): IResultadoAnalisador {
  const resultado = {
    nome: nome,
    resumo: tabelas.resumo,
    opcoes: [] as IOpcaoAnalisador[]
  }

  if (tabelas.salariosCompleta.length) {
    resultado.opcoes.push({
      nome: 'Salários-de-contribuição',
      parametros: [
        {
          intervalo: 'SalariosContribuicao',
          dados: tabelas.salariosCompleta,
          competenciaInicial: tabelas.competenciaInicial,
          competenciaFinal: tabelas.competenciaFinal
        }
      ]
    })
  }

  if (tabelas.periodos.length) {
    resultado.opcoes.push({
      nome: 'Períodos',
      parametros: [
        {
          intervalo: 'TCInformado',
          dados: tabelas.periodos
        }
      ]
    })
  }

  resultado.opcoes.push({
    nome: 'Nova Página',
    parametros: [
      {
        intervalo: '',
        dados: tabelas.novaPagina,
        competenciaInicial: tabelas.competenciaInicial,
        competenciaFinal: tabelas.competenciaFinal
      }
    ]
  })

  return resultado
}
