import type { IModulo, IResultadoAnalisador } from '../comuns'
import type { IDadosProcesso } from './tipos'
import { extrairValorComPadrao, converterStringEmData } from '@cecalc/utils'
import gerarOpcoes from './gerar-opcoes'
import gerarTabelas from './gerar-tabelas'

const SisJef: IModulo = class Modulo {
  static nome = 'Dados do Processo (SisJEF)'

  static padrao = {
    processo: /Processo\.+:\s(\d{7}-\d{2}\.\d{4}\.\d{1}\.\d{2}\.\d{4})/,
    protocolo: /Dt\.Protoc\.:\s(\d{2}\/\d{2}\/\d{4})/,
    autor: /AUTOR\.+:\s\d+\s-\s(.+)\s(?=Advogado)/,
    reu: /RÉU\.+:\s\d+\s-\s(.+)\s(?=Advogado)/,
    citacao: /Dt\.Citação Réu\.+:\s(\d{2}\/\d{2}\/\d{4})/
  }

  static reiniciarPadroes() {
    Modulo.padrao.processo.lastIndex = 0
    Modulo.padrao.protocolo.lastIndex = 0
    Modulo.padrao.autor.lastIndex = 0
    Modulo.padrao.reu.lastIndex = 0
    Modulo.padrao.citacao.lastIndex = 0
  }

  static testar(texto: string): boolean {
    Modulo.reiniciarPadroes()
    return (
      Modulo.padrao.processo.test(texto) &&
      Modulo.padrao.protocolo.test(texto) &&
      Modulo.padrao.autor.test(texto)
    )
  }

  public analisar(texto: string): IResultadoAnalisador {
    Modulo.reiniciarPadroes()

    const dados = {} as IDadosProcesso

    const processo = extrairValorComPadrao(texto, Modulo.padrao.processo)
    const protocolo = extrairValorComPadrao(texto, Modulo.padrao.protocolo)
    const autor = extrairValorComPadrao(texto, Modulo.padrao.autor)
    const reu = extrairValorComPadrao(texto, Modulo.padrao.reu)
    const citacao = extrairValorComPadrao(texto, Modulo.padrao.citacao)

    dados.processo = processo?.trim() ?? ''
    if (protocolo) dados.protocolo = converterStringEmData(protocolo.trim())
    dados.autor = autor?.trim() ?? ''
    dados.reu = reu?.trim() ?? ''
    if (citacao) dados.citacao = converterStringEmData(citacao.trim())

    const tabelas = gerarTabelas(dados)

    return gerarOpcoes(Modulo.nome, tabelas, dados)
  }
}

export default SisJef
