import { tipoString } from '../predicados';
import { semAcentuacao } from './semAcentuacao';

/**
 * Remove acentuação e caracteres especiais de uma string.
 * Lança um erro se o parâmetro de entrada não é uma string.
 *
 * @param str
 */
export function somentePalavrasAlfanumericas(str: string): string {
  if (!tipoString(str)) throw new Error('A função aceita somente strings!');
  return semAcentuacao(str).replace(/[^\s\w]/g, '');
}
