import { tipoNumero } from '../predicados';

/**
 * Valida ano, mês e dia, checando se todos os parâmetros representam uma data real.
 *
 * @param ano
 * @param mes
 * @param dia
 */
export function validarParametrosDeData(ano: number, mes: number, dia: number): boolean {
  if (!tipoNumero(ano) || !tipoNumero(mes) || !tipoNumero(dia)) return false;
  if (ano < 0 || mes < 1 || mes > 12 || dia < 1 || dia > 31) return false;
  const data = new Date(ano, mes - 1, dia);
  return ano === data.getFullYear() && mes === data.getMonth() + 1 && dia === data.getDate();
}
