import { depoisDe } from './depoisDe';
import { antesDe } from './antesDe';

/**
 * Verifica se uma data está entre outras duas datas
 *
 * @param data
 * @param primeira
 * @param segunda
 */
export function dataEntre(data: Date, primeira: Date, segunda: Date): boolean {
  return primeira.valueOf() < segunda.valueOf()
    ? depoisDe(data, primeira) && antesDe(data, segunda)
    : depoisDe(data, segunda) && antesDe(data, primeira);
}
