import type { IResultadoAnalisador } from '../comuns'
import type { IDadosProcesso, ITabelasProcesso } from './tipos'

export default function gerarOpcoes(
  nome: string,
  tabelas: ITabelasProcesso,
  dados: IDadosProcesso
): IResultadoAnalisador {
  return {
    nome: nome,
    resumo: tabelas.resumo,
    opcoes: [
      {
        nome: 'Dados do Processo',
        parametros: [
          {
            intervalo: 'Processo',
            dados: [[dados.processo]]
          },
          {
            intervalo: 'Protocolo',
            dados: [[dados.protocolo]]
          },
          {
            intervalo: 'Autor',
            dados: [[dados.autor]]
          },
          {
            intervalo: 'Reu',
            dados: [[dados.reu]]
          },
          {
            intervalo: 'Citacao',
            dados: [[dados.citacao]]
          }
        ]
      },
      {
        nome: 'Nova Página',
        parametros: [
          {
            intervalo: '',
            dados: tabelas.novaPagina
          }
        ]
      }
    ]
  }
}
