import { tipoNumero } from '../predicados';
import { validarData } from '../validacao';

/**
 * Retorna o último dia do mês, permitindo adicionar ou substrair meses.
 *
 * Uso:
 * ```
 * console.log(ultimodiaDoMes(new Date(2020, 0, 1)));
 * // Saída: Fri Jan 31 2020 00:00:00 GMT-0000 (UTC)
 * ```
 *
 * @param data
 * @param mesesParaAdicionar
 */
export function ultimoDiaDoMes(data: Date, mesesParaAdicionar?: number): Date {
  if (!validarData(data)) throw new Error('A função aceita somente datas válidas!');
  mesesParaAdicionar = tipoNumero(mesesParaAdicionar) ? mesesParaAdicionar : 0;
  return new Date(data.getFullYear(), data.getMonth() + 1 + mesesParaAdicionar, 0);
}
