import { semAcentuacao } from './semAcentuacao';
import { limparString } from './limparString';
import { somentePalavrasAlfanumericas } from './somentePalavrasAlfanumericas';

/**
 * Remove acentuação, caracteres de controle, caracteres não alfanuméricos e apara a string, retirando espaços iniciais e finais.
 * Lança um erro se o parâmetro de entrada não é uma string (pelas outras funções invocadas).
 *
 * @param str
 */
export function normalizar(str: string): string {
  return somentePalavrasAlfanumericas(limparString(semAcentuacao(str)));
}
