import type { IModulo, IResultadoAnalisador } from '../comuns'
import type { IDadosBeneficioPlenus, IDadosPlenus, IDadosSalarioPlenus } from './tipos'
import {
  limparString,
  converterEmNumero,
  extrairValorComPadrao,
  converterStringEmData,
  CData
} from '@cecalc/utils'
import gerarTabelas from './gerar-tabelas'
import gerarOpcoes from './gerar-opcoes'

const Plenus: IModulo = class Modulo {
  static nome = 'Plenus'

  static padrao = {
    nome: /NB\s>\d+<\s(.*?)(?=\sTp\.Calculo)/,
    dataNascimento: /(?:Data de Nascimento\s*|Dt.Nascimento\ssegurado\s*):\s(\d{2}\/\d{2}\/\d{4})/,
    idade: /Idade do Beneficiario:\s([\d]+)/,
    nb: /NB\s>(\d+)/,
    especie: /(?:Especie|Esp\.):\s(\d+)/,
    der: /DER:\s(\d{2}\/\d{2}\/\d{4})/,
    dib: /DIB:\s(\d{2}\/\d{2}\/\d{4})/,
    ddb: /DDB:\s(\d{2}\/\d{2}\/\d{4})/,
    dip: /DIP:\s(\d{2}\/\d{2}\/\d{4})/,
    dcb: /DCB:\s(\d{2}\/\d{2}\/\d{4})/,
    // Grupos: 1) ano, 2) mês, 3) dias.
    tc: /Tempo de Contribuicao:\s(\d+)A\s(\d+)M\s(\d+)D/,
    expectativaSobrevida: /Expectativa de Sobrevida:\s([\d,]+)/,
    fatorPrevindenciario: /Fator Previden\.:\s([\d,]+)/,
    coeficiente: /Coeficiente:\s([\d,]+)/,
    salarioDeBeneficio: /Sal\.Beneficio:\s([\d.,]+)/,
    rmi: /RMI:\s([\d.,]+)/,
    rma: /Reajustada - MR :\s([\d.,]+)/,
    indiceTeto: /Teto:\s([\d.,]+)/,
    // Grupos: 1) número sequencial, 2) competência, 3) salário, 4) índice, 5) salário corrigido, 6) desconsiderado (opcional).
    salarios: /(\d{3})\s+(\d{2}\/\d{4})\s+([\d.,]+)\s+([\d.,]+)\s+([\d.,]+)\s*(:?DESCONSIDERADO)?/g
  }

  static reiniciarPadroes() {
    Modulo.padrao.nome.lastIndex = 0
    Modulo.padrao.dataNascimento.lastIndex = 0
    Modulo.padrao.idade.lastIndex = 0
    Modulo.padrao.nb.lastIndex = 0
    Modulo.padrao.especie.lastIndex = 0
    Modulo.padrao.der.lastIndex = 0
    Modulo.padrao.dib.lastIndex = 0
    Modulo.padrao.ddb.lastIndex = 0
    Modulo.padrao.dip.lastIndex = 0
    Modulo.padrao.dcb.lastIndex = 0
    Modulo.padrao.tc.lastIndex = 0
    Modulo.padrao.expectativaSobrevida.lastIndex = 0
    Modulo.padrao.fatorPrevindenciario.lastIndex = 0
    Modulo.padrao.coeficiente.lastIndex = 0
    Modulo.padrao.salarioDeBeneficio.lastIndex = 0
    Modulo.padrao.rmi.lastIndex = 0
    Modulo.padrao.rma.lastIndex = 0
    Modulo.padrao.indiceTeto.lastIndex = 0
    Modulo.padrao.salarios.lastIndex = 0
  }

  static testar(texto: string): boolean {
    Modulo.reiniciarPadroes()
    return (
      Modulo.padrao.salarios.test(texto) ||
      (Modulo.padrao.nome.test(texto) && Modulo.padrao.nb.test(texto))
    )
  }

  public analisar(texto: string): IResultadoAnalisador {
    Modulo.reiniciarPadroes()

    const dados: IDadosPlenus = {
      beneficio: {} as IDadosBeneficioPlenus,
      salarios: [] as IDadosSalarioPlenus[]
    }

    const segurado = extrairValorComPadrao(texto, Modulo.padrao.nome)
    const dataNascimento = extrairValorComPadrao(texto, Modulo.padrao.dataNascimento)
    const idade = extrairValorComPadrao(texto, Modulo.padrao.idade)
    const nb = extrairValorComPadrao(texto, Modulo.padrao.nb)
    const especie = extrairValorComPadrao(texto, Modulo.padrao.especie)
    const der = extrairValorComPadrao(texto, Modulo.padrao.der)
    const dib = extrairValorComPadrao(texto, Modulo.padrao.dib)
    const ddb = extrairValorComPadrao(texto, Modulo.padrao.ddb)
    const dip = extrairValorComPadrao(texto, Modulo.padrao.dip)
    const dcb = extrairValorComPadrao(texto, Modulo.padrao.dcb)
    const expectativaSobrevida = extrairValorComPadrao(texto, Modulo.padrao.expectativaSobrevida)
    const fatorPrevidenciario = extrairValorComPadrao(texto, Modulo.padrao.fatorPrevindenciario)
    const coeficiente = extrairValorComPadrao(texto, Modulo.padrao.coeficiente)
    const salarioBeneficio = extrairValorComPadrao(texto, Modulo.padrao.salarioDeBeneficio)
    const rmi = extrairValorComPadrao(texto, Modulo.padrao.rmi)
    const rma = extrairValorComPadrao(texto, Modulo.padrao.rma)
    const indiceTeto = extrairValorComPadrao(texto, Modulo.padrao.indiceTeto)

    dados.beneficio.segurado = segurado ?? ''
    if (dataNascimento) dados.beneficio.dataNascimento = converterStringEmData(dataNascimento)
    if (idade) dados.beneficio.idade = converterEmNumero(idade)
    dados.beneficio.nb = nb ?? ''
    if (especie) dados.beneficio.especie = converterEmNumero(especie)
    if (der) dados.beneficio.der = converterStringEmData(der)
    if (dib) dados.beneficio.dib = converterStringEmData(dib)
    if (ddb) dados.beneficio.ddb = converterStringEmData(ddb)
    if (dip) dados.beneficio.dip = converterStringEmData(dip)
    if (dcb) dados.beneficio.dcb = converterStringEmData(dcb)
    if (expectativaSobrevida)
      dados.beneficio.expectativaSobrevida = converterEmNumero(expectativaSobrevida)
    if (fatorPrevidenciario)
      dados.beneficio.fatorPrevidenciario = converterEmNumero(fatorPrevidenciario)
    if (coeficiente) dados.beneficio.coeficiente = converterEmNumero(coeficiente) / 100
    if (salarioBeneficio) dados.beneficio.salarioBeneficio = converterEmNumero(salarioBeneficio)
    if (rmi) dados.beneficio.rmi = converterEmNumero(rmi)
    if (rma) dados.beneficio.rma = converterEmNumero(rma)
    if (indiceTeto) dados.beneficio.indiceTeto = converterEmNumero(indiceTeto)

    dados.beneficio.calcularAbono =
      dados.beneficio.especie !== 87 &&
      dados.beneficio.especie !== 88 &&
      dados.beneficio.especie !== 95

    const tempo = Modulo.padrao.tc.exec(texto)
    if (Array.isArray(tempo) && tempo.length >= 4) {
      dados.beneficio.TCAnos = converterEmNumero(tempo[1])
      dados.beneficio.TCMeses = converterEmNumero(tempo[2])
      dados.beneficio.TCDias = converterEmNumero(tempo[3])
    }

    const temp = texto.match(Modulo.padrao.salarios)

    if (Array.isArray(temp)) {
      temp.forEach(salario => {
        const item = limparString(salario).split(' ')
        if (Array.isArray(item) && item.length >= 5) {
          dados.salarios.push({
            seq: converterEmNumero(item[0]),
            competencia: new CData(item[1]),
            valorOriginal: converterEmNumero(item[2]),
            indice: converterEmNumero(item[3]),
            valorCorrigido: converterEmNumero(item[4]),
            desconsiderado: item.length >= 6 && item[5] === 'DESCONSIDERADO'
          })
        }
      })
    }

    const tabelas = gerarTabelas(dados)

    return gerarOpcoes(Modulo.nome, tabelas, dados)
  }
}

export default Plenus
