import { anoBissexto } from './anoBissexto';
import { depoisDe } from './depoisDe';
import { antesDe } from './antesDe';
import { dataEntre } from './dataEntre';
import { mesmaData } from './mesmaData';
import { converterStringEmData } from './converterStringEmData';
import { converterDataEmString } from './converterDataEmString';
import { tipoString } from '../predicados';
import { validarData, validarParametrosDeData, validarParametrosDeHora } from '../validacao';
import { FORMATO_DATA, TIPO_DURACAO } from '../enums';
import { primeiroDiaDoMes } from './primeiroDiaDoMes';
import { adicionarNaData } from './adicionarNaData';

/**
 * Classe criada para facilitar o trabalho com o objeto nativo `Date`.
 *
 * Exemplo:
 * ```
 * console.log(new CData('2020-01-01').humanizada());
 * // Saída: 1º de janeiro de 2020
 * ```
 */
export class CData {
  private data: Date | undefined;
  private valida = false;

  /**
   * Quando nenhum argumento é passado ao construtor, cria um objeto data utilizando `new Date()`.
   */
  constructor();
  /**
   * Quando uma data é passada como parâmetro para o construtor, ela é copiada (clonada).
   *
   * @param valor
   */
  constructor(valor: Date);
  /**
   * Quando uma string é passada como parâmetro para o construtor, ela é convertida em data utilizando a função `converterStringEmData`.
   *
   * @param stringAConverter
   */
  constructor(stringAConverter: string);
  /**
   * Quando números são passados como parâmetros para o construtor, eles são validados e repassados ao construtor do objeto `Date` nativo.
   * Considerando a validação prévia, diferentemente do comportamento nativo, parâmetros como `new CData(2020, 1, 30)` ou `new CData(2020, 12, 31)` não são aceitos como válidos.
   *
   * @param ano
   * @param indiceMes
   * @param dia
   * @param hora
   * @param minutos
   * @param segundos
   * @param milisegundos
   */
  constructor(
    ano: number,
    indiceMes: number,
    dia?: number,
    hora?: number,
    minutos?: number,
    segundos?: number,
    milisegundos?: number
  );
  constructor(...args: unknown[]) {
    if (args.length === 0) {
      this.data = new Date();
      this.valida = validarData(this.data);
    }
    if (args.length === 1) {
      if (validarData(args[0])) {
        this.data = new Date(args[0].valueOf());
        this.valida = true;
      }
      if (tipoString(args[0])) {
        try {
          this.data = CData.converterStringEmData(args[0]);
          this.valida = validarData(this.data);
          // eslint-disable-next-line no-empty
        } catch (erro) {}
      }
    }
    if (args.length >= 2) {
      const [ano, indiceMes] = args.slice(0, 2);
      let [dia, hora, minutos, segundos, milisegundos] = args.slice(2);
      dia = dia === undefined ? 1 : dia;
      hora = hora === undefined ? 0 : hora;
      minutos = minutos === undefined ? 0 : minutos;
      segundos = segundos === undefined ? 0 : segundos;
      milisegundos = milisegundos === undefined ? 0 : milisegundos;
      const dataValida = validarParametrosDeData(ano as number, (indiceMes as number) + 1, dia as number);
      const horaValida = validarParametrosDeHora(
        hora as number,
        minutos as number,
        segundos as number,
        milisegundos as number
      );
      if (dataValida && horaValida) {
        this.data = new Date(
          ano as number,
          indiceMes as number,
          dia as number,
          hora as number,
          minutos as number,
          segundos as number,
          milisegundos as number
        );
        this.valida = validarData(this.data);
      }
    }
    if (!this.valida) this.data = undefined;
    return this;
  }

  /**
   * Converter uma string em data, utilizando a função `converterStringEmData`.
   *
   * @param stringAConverter
   */
  static converterStringEmData(stringAConverter: string): Date {
    return converterStringEmData(stringAConverter);
  }

  /**
   * Retorna o objeto nativo.
   * Lança erro se a data subjacente for inválida.
   */
  nativo(): Date {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return this.data;
  }

  /**
   * Retorna o valor de `valueOf()` do objeto nativo.
   * Lança erro se a data subjacente for inválida.
   */
  valueOf(): number {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return this.data.valueOf();
  }

  /**
   * Retorna a competência correspondente à data subjacente.
   * Lança erro se a data subjacente for inválida.
   */
  competencia(): Date {
    return primeiroDiaDoMes(this.nativo());
  }

  /**
   * Retorna a competência em formato de string localizada.
   * Lança erro se a data subjacente for inválida.
   */
  competenciaComoString(): string {
    return converterDataEmString(this.competencia(), FORMATO_DATA.LOCALIDADE_ABREVIADO);
  }

  /**
   * Informa se a data subjacente é válida.
   */
  dataValida(): boolean {
    return this.valida;
  }

  /**
   * Informa se a data subjacente cai em ano bissexto.
   * Lança erro se a data subjacente for inválida.
   */
  anoBissexto(): boolean {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return anoBissexto(this.data.getFullYear());
  }

  /**
   * Informa se a data subjacente é posterior à data fornecida como parâmetro.
   * Lança erro se a data subjacente for inválida.
   *
   * @param ref
   */
  depoisDe(ref: Date): boolean {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return depoisDe(this.data, ref);
  }

  /**
   * Informa se a data subjacente é anterior à data fornecida como parâmetro.
   * Lança erro se a data subjacente for inválida.
   *
   * @param ref
   */
  antesDe(ref: Date): boolean {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return antesDe(this.data, ref);
  }

  /**
   * Informa se a data subjacente entre as datas fornecidas como parâmetro.
   * Lança erro se a data subjacente for inválida.
   *
   * @param primeira
   * @param segunda
   */
  estaEntre(primeira: Date, segunda: Date): boolean {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return dataEntre(this.data, primeira, segunda);
  }

  /**
   * Informa se a data subjacente coincide com a data fornecida como parâmetro, levando em conta apenas dia, mês e ano.
   * Lança erro se a data subjacente for inválida.
   *
   * @param ref
   */
  igualA(ref: Date): boolean {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return mesmaData(this.data, ref);
  }

  /**
   * Adiciona ou subtrai certo número de anos, meses, semanas, dias, horas, minutos ou segundos da data subjacente, retornando um novo objeto CData.
   * Lança erro se a data subjacente for inválida.
   *
   * @param unidades Unidades para adicionar (ou subtrair, se o número for negativo)
   * @param tipoDuracao
   */
  adicionar(unidades: number, tipoDuracao: TIPO_DURACAO = TIPO_DURACAO.DIAS): CData {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return new CData(adicionarNaData(this.data, unidades, tipoDuracao));
  }

  /**
   * Retorna a data subjacente no formato ISO-8061 abreviado.
   * Lança erro se a data subjacente for inválida.
   */
  formatoISOAbreviado(): string {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return converterDataEmString(this.data, FORMATO_DATA.ISO_ABREVIADO);
  }

  /**
   * Alias para o método `formatoISOAbreviado`.
   */
  toString(): string {
    return this.formatoISOAbreviado();
  }

  /**
   * Retorna a data subjacente no formato ISO-8061 completo.
   * Lança erro se a data subjacente for inválida.
   */
  formatoISO(): string {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return converterDataEmString(this.data, FORMATO_DATA.ISO);
  }

  /**
   * Retorna a data subjacente no formato string localizada abreviada.
   * Lança erro se a data subjacente for inválida.
   */
  local(): string {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return converterDataEmString(this.data, FORMATO_DATA.LOCALIDADE_ABREVIADO);
  }

  /**
   * Retorna a data subjacente em formato "humanizado" (string localizada completa).
   * Lança erro se a data subjacente for inválida.
   */
  humanizada(): string {
    if (!this.valida || this.data === undefined) throw new Error('Data inválida!');
    return converterDataEmString(this.data, FORMATO_DATA.LOCALIDADE);
  }
}
