/**
 * Verifica se duas datas coincidem, considerando somente dia, mês e ano.
 *
 * @param a
 * @param b
 */
export function mesmaData(a: Date, b: Date): boolean {
  return (
    a.getUTCFullYear() === b.getUTCFullYear() &&
    a.getUTCMonth() === b.getUTCMonth() &&
    a.getUTCDate() === b.getUTCDate()
  );
}
