import type { IDadosProcesso, ITabelasProcesso, TResumoProcesso } from './tipos'

export default function gerarTabelas(dados: IDadosProcesso): ITabelasProcesso {
  const novaPagina = [
    ['Processo:', dados.processo],
    ['Autor:', dados.autor],
    ['Réu:', dados.reu],
    ['Protocolo:', dados.protocolo],
    ['Citação:', dados.citacao]
  ]

  const resumo: TResumoProcesso = [
    ['Processo:', dados.processo],
    ['Autor(a):', dados.autor]
  ]

  return { resumo, novaPagina }
}
