import type { IModulo, IResultadoAnalisador } from '../comuns'
import type { IDadosBeneficioHiscre, IDadosPagamentoHiscre, IDadosHiscre } from './tipos'
import {
  converterEmNumero,
  extrairValorComPadrao,
  converterStringEmData,
  CData
} from '@cecalc/utils'
import gerarTabelas from './gerar-tabelas'
import gerarOpcoes from './gerar-opcoes'

const Hiscre: IModulo = class Modulo {
  static nome = 'Histórico de Créditos'

  static padrao = {
    nb: /NB:\s(\d+)/,
    nome: /NB:\s\d+\s-\s(.*)/,
    especie: /Espécie:\s(\d+)/,
    dib: /\(DIB\):\s(\d{2}\/\d{2}\/\d{4})/,
    dcb: /\(DCB\):\s(\d{2}\/\d{2}\/\d{4})/,
    dip: /\(DIP\):\s(\d{2}\/\d{2}\/\d{4})/,
    rendaMensal: /MR:\s(?:R\$\s)?([\d,]+)/,
    blocos: /(?:Comp)([\s\S]*?)(?=Comp|$)/g,
    subBlocos: {
      // Grupos: 1) competência, 2) data inicial, 3) data final, 4) valor líquido, 5) data pagamento.
      periodo:
        /(\d{2}\/\d{4})\s(\d{2}\/\d{2}\/\d{4})\sa\s(\d{2}\/\d{2}\/\d{4})\sR\$\s([\d,]+)\D+(\d{2}\/\d{2}\/\d{4})/,
      // Grupos: 1) código, 2) valor.
      discriminacao: /^(\d{3})\D+?([\d.,]+)$/gm
    }
  }

  static reiniciarPadroes() {
    Modulo.padrao.nb.lastIndex = 0
    Modulo.padrao.nome.lastIndex = 0
    Modulo.padrao.especie.lastIndex = 0
    Modulo.padrao.dib.lastIndex = 0
    Modulo.padrao.dcb.lastIndex = 0
    Modulo.padrao.dip.lastIndex = 0
    Modulo.padrao.rendaMensal.lastIndex = 0
    Modulo.padrao.blocos.lastIndex = 0
    Modulo.padrao.subBlocos.periodo.lastIndex = 0
    Modulo.padrao.subBlocos.discriminacao.lastIndex = 0
  }

  static testar(texto: string): boolean {
    Modulo.reiniciarPadroes()
    return Modulo.padrao.blocos.test(texto) && Modulo.padrao.subBlocos.periodo.test(texto)
  }

  public analisar(texto: string): IResultadoAnalisador {
    Modulo.reiniciarPadroes()

    const dados: IDadosHiscre = {
      beneficio: {} as IDadosBeneficioHiscre,
      pagamentos: [] as IDadosPagamentoHiscre[]
    }

    const segurado = extrairValorComPadrao(texto, Modulo.padrao.nome)
    const nb = extrairValorComPadrao(texto, Modulo.padrao.nb)
    const especie = extrairValorComPadrao(texto, Modulo.padrao.especie)
    const dib = extrairValorComPadrao(texto, Modulo.padrao.dib)
    const dcb = extrairValorComPadrao(texto, Modulo.padrao.dcb)
    const dip = extrairValorComPadrao(texto, Modulo.padrao.dip)
    const rm = extrairValorComPadrao(texto, Modulo.padrao.rendaMensal)

    dados.beneficio.segurado = segurado ?? ''
    dados.beneficio.nb = nb ?? ''
    if (especie) dados.beneficio.especie = converterEmNumero(especie)
    if (dib) dados.beneficio.dib = converterStringEmData(dib)
    if (dcb) dados.beneficio.dcb = converterStringEmData(dcb)
    if (dip) dados.beneficio.dip = converterStringEmData(dip)
    if (rm) dados.beneficio.rm = converterEmNumero(rm)

    const temp = texto.match(Modulo.padrao.blocos)

    if (Array.isArray(temp)) {
      temp.forEach(pagamento => {
        const item = {} as IDadosPagamentoHiscre
        const periodo = Modulo.padrao.subBlocos.periodo.exec(pagamento)
        if (Array.isArray(periodo) && periodo.length >= 6) {
          item.competencia = new CData(periodo[1])
          item.dataInicial = new CData(periodo[2])
          item.dataFinal = new CData(periodo[3])
          item.valorLiquido = converterEmNumero(periodo[4])
          item.dataPagamento = new CData(periodo[5])
        }

        const discriminacao = pagamento.match(Modulo.padrao.subBlocos.discriminacao)
        if (Array.isArray(discriminacao)) {
          item.discriminacao = discriminacao.map(rubrica => {
            const partes = rubrica.split(' ')
            const codigo = converterEmNumero(partes[0])
            const valor = converterEmNumero(partes[partes.length - 1])
            return { codigo, valor }
          })
        }

        if (item.competencia) dados.pagamentos.push(item)
      })
    }

    const tabelas = gerarTabelas(dados)

    return gerarOpcoes(Modulo.nome, tabelas, dados)
  }
}

export default Hiscre
