/**
 * Um modo mais conveniente de checar os tipos em Javascript, no lugar de `typeof` e `instanceof`.
 *
 * @param o
 */
export function tipo(o: unknown): string {
  // @ts-expect-error O tipo está sendo verificado
  return {}.toString
    .call(o)
    .match(/\s([a-zA-Z]+)/)[1]
    .toLowerCase();
}
