import { tipoNumero } from '../predicados';
import { validarData } from '../validacao';
import { TIPO_DURACAO } from '../enums';

/**
 * Adiciona (ou subtrai) um número de anos, meses, semanas, dias, horas, minutos ou segundos da data fornecida.
 *
 * Uso:
 * ```
 * console.log(adicionarNaData(new Date(2020, 0, 31), 5, TIPO_DURACAO.DIAS));
 * // Saída: Wed Feb 05 2020 00:00:00 GMT-0000 (UTC)
 * ```
 *
 * @param data
 * @param unidades
 */
export function adicionarNaData(data: Date, unidades: number, tipoDuracao: TIPO_DURACAO = TIPO_DURACAO.DIAS): Date {
  if (!validarData(data)) throw new Error('A função aceita somente datas válidas!');

  unidades = tipoNumero(unidades) ? unidades : 0;

  switch (tipoDuracao) {
    case TIPO_DURACAO.ANOS:
      return new Date(
        data.getFullYear() + unidades,
        data.getMonth(),
        data.getDate(),
        data.getHours(),
        data.getMinutes(),
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.MESES:
      return new Date(
        data.getFullYear(),
        data.getMonth() + unidades,
        data.getDate(),
        data.getHours(),
        data.getMinutes(),
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.DIAS:
      return new Date(
        data.getFullYear(),
        data.getMonth(),
        data.getDate() + unidades,
        data.getHours(),
        data.getMinutes(),
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.SEMANAS:
      return new Date(
        data.getFullYear(),
        data.getMonth(),
        data.getDate() + unidades * 7,
        data.getHours(),
        data.getMinutes(),
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.HORAS:
      return new Date(
        data.getFullYear(),
        data.getMonth(),
        data.getDate(),
        data.getHours() + unidades,
        data.getMinutes(),
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.MINUTOS:
      return new Date(
        data.getFullYear(),
        data.getMonth(),
        data.getDate(),
        data.getHours(),
        data.getMinutes() + unidades,
        data.getSeconds(),
        data.getMilliseconds()
      );

    case TIPO_DURACAO.SEGUNDOS:
      return new Date(
        data.getFullYear(),
        data.getMonth(),
        data.getDate(),
        data.getHours(),
        data.getMinutes(),
        data.getSeconds() + unidades,
        data.getMilliseconds()
      );

    default:
      throw new Error('Parâmetro de duração inválido!');
  }
}
