import { validarData } from '../validacao';

/**
 * Verifica se uma data é anterior a outra
 *
 * @param data
 * @param ref
 */
export function antesDe(data: Date, ref: Date): boolean {
  if (!validarData(data) || !validarData(ref)) throw new Error('Data inválida!');
  return data.valueOf() < ref.valueOf();
}
