import type { IModulo, IResultadoAnalisador } from '../comuns'
import type { IDadosCnis, IDadosSeguradoCnis, IDadosVinculoCnis } from './tipos'
import { limparString, CData, converterEmNumero, stringNaoVazia, objetoVazio } from '@cecalc/utils'
import gerarTabelas from './gerar-tabelas'
import gerarOpcoes from './gerar-opcoes'

const CnisVinculos: IModulo = class Modulo {
  static nome = 'CNIS Vínculos'

  static padrao = {
    // Grupos: 1) nome, 2) mãe, 3) NIT, 4) data de nascimento, 5) CPF.
    dadosSegurado:
      /Nit: Nome:\sData de Nascimento: Nome da Mãe:\s(.*)\s(.*)\s([\d.-]+)\s(\d{2}\/\d{2}\/\d{4})\sCPF:\s([\d.-]+)/,
    // Grupos: 1) NIT, 2) CPF, 3) nome, 4) data de nascimento, 5) mãe.
    dadosSeguradoAlt:
      /Identificação do Filiado Nit:\s([\d.-]+)\sCPF:\s([\d.-]+)\sNome:\s(.*)\sData de Nascimento:\s(\d{2}\/\d{2}\/\d{4})\sNome da Mãe:\s(.*)\sRelações Previdenciárias/,
    vinculos:
      /(\d{1,3})\s(\d\.\d{3}\.\d{3}\.\d{3}-\d)\s([\W\w]+?)\s+(\d{0,2}\/?\d{1,2}\/\d{2,4})\s(\d{0,2}\/?\d{1,2}\/\d{2,4})/g,
    vinculo:
      /(\d{1,3})\s(\d\.\d{3}\.\d{3}\.\d{3}-\d)\s([\W\w]+?)\s+(\d{0,2}\/?\d{1,2}\/\d{2,4})\s(\d{0,2}\/?\d{1,2}\/\d{2,4})/
  }

  static reiniciarPadroes() {
    Modulo.padrao.dadosSegurado.lastIndex = 0
    Modulo.padrao.dadosSeguradoAlt.lastIndex = 0
    Modulo.padrao.vinculos.lastIndex = 0
    Modulo.padrao.vinculo.lastIndex = 0
  }

  static testar(texto: string): boolean {
    Modulo.reiniciarPadroes()
    return Modulo.padrao.vinculos.test(texto)
  }

  public analisar(texto: string): IResultadoAnalisador {
    Modulo.reiniciarPadroes()

    const dados: IDadosCnis = {
      segurado: {} as IDadosSeguradoCnis,
      vinculos: [] as IDadosVinculoCnis[]
    }

    const normal = Modulo.padrao.dadosSegurado.test(texto)

    const dadosSegurado = normal
      ? Modulo.padrao.dadosSegurado.exec(texto)
      : Modulo.padrao.dadosSeguradoAlt.exec(texto)

    if (Array.isArray(dadosSegurado) && dadosSegurado.length >= 6) {
      dados.segurado.nome = normal ? limparString(dadosSegurado[1]) : limparString(dadosSegurado[3])
      dados.segurado.mae = normal ? limparString(dadosSegurado[2]) : limparString(dadosSegurado[5])
      dados.segurado.nit = normal ? limparString(dadosSegurado[3]) : limparString(dadosSegurado[1])
      dados.segurado.nascimento = new CData(dadosSegurado[4]).nativo()
      dados.segurado.cpf = normal ? limparString(dadosSegurado[5]) : limparString(dadosSegurado[2])
    }

    // Primeiro varremos todos os blocos em busca dos vínculos
    dados.vinculos = texto
      .match(Modulo.padrao.vinculos)! // sabemos que o objeto não é nulo, porque um dos critérios de teste é a presença de vínculos
      .map(vinculo => {
        const item = {} as IDadosVinculoCnis
        const dados = Modulo.padrao.vinculo.exec(vinculo)
        if (Array.isArray(dados) && dados.length >= 6) {
          item.seq = converterEmNumero(dados[1])
          item.nit = dados[2]
          item.descricao = limparString(dados[3])
          item.dataInicial = new CData(dados[4].trim())
          item.dataFinal = stringNaoVazia(dados[5]) ? new CData(dados[5].trim()) : null
        }
        return item
      })
      .filter(item => !objetoVazio(item))
      .sort((a, b) => a.seq - b.seq)

    const tabelas = gerarTabelas(dados, true)

    return gerarOpcoes(Modulo.nome, tabelas)
  }
}

export default CnisVinculos
