import { CData, concatenarTabelas, intervaloCompetencias, TCelula, TTabela } from '@cecalc/utils'
import type { IDadosContribuicaoCnis, IDadosCnis, ITabelasCnis, TResumoCnis } from './tipos'

function analisarDescricaoAtividade(descricao: string): string[] {
  descricao = descricao.replace(/\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}\s/, '')
  const tipos = [
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Empresário \/ Empregador)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Empresário)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Empregador)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Trabalhador Avulso)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Trabalhador)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Contribuinte Individual)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Contribuinte)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Empregado Doméstico)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Empregado)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Autônomo)$/,
    /([A-ZÁÉÍÓÚÃÕ /]+)\s[\D\d\s]*?(Não Informado)$/,
    /([\D\d]+)\s(Empresário \/ Empregador)$/,
    /([\D\d]+)\s(Empresário)$/,
    /([\D\d]+)\s(Empregador)$/,
    /([\D\d]+)\s(Trabalhador Avulso)$/,
    /([\D\d]+)\s(Trabalhador)$/,
    /([\D\d]+)\s(Contribuinte Individual)$/,
    /([\D\d]+)\s(Contribuinte)$/,
    /([\D\d]+)\s(Empregado Doméstico)$/,
    /([\D\d]+)\s(Empregado)$/,
    /([\D\d]+)\s(Autônomo)$/,
    /([\D\d]+)\s(Não Informado)$/
  ]
  for (let i = 0; i < tipos.length; i++) {
    if (tipos[i].test(descricao)) {
      const analisado = tipos[i].exec(descricao)
      return Array.isArray(analisado) ? analisado : ['', '', '']
    }
  }
  return [descricao, descricao, '']
}

function totalizarValoresContribuicoes(valores: IDadosContribuicaoCnis[], competencia: Date) {
  return valores
    .filter(item => item.competencia.igualA(competencia))
    .reduce((resultado, item) => {
      resultado += item.valor
      return resultado
    }, 0)
}

export default function gerarTabelas(dados: IDadosCnis, excluirSalarios = false): ITabelasCnis {
  const info: TTabela<TCelula> = [
    ['Segurado:', dados.segurado.nome || ''],
    ['NIT:', dados.segurado.nit || ''],
    ['CPF:', dados.segurado.cpf || ''],
    ['Mãe:', dados.segurado.mae || ''],
    ['Data de Nascimento:', dados.segurado.nascimento || '']
  ]

  const inicial = dados.vinculos.reduce((data, vinculo) => {
    return vinculo.dataInicial.depoisDe(data.nativo()) || vinculo.dataInicial.igualA(data.nativo())
      ? data
      : vinculo.dataInicial
  }, new CData())

  const final = dados.vinculos.reduce((data, vinculo) => {
    const final = vinculo.dataFinal || new CData()
    return final.antesDe(data.nativo()) ? data : final
  }, inicial)

  const competenciaInicial = inicial.competencia()
  const competenciaFinal = final.competencia()

  const out1964 = new Date(1964, 9, 1, 0, 0, 0, 0)
  const atual = new Date(new Date().getFullYear(), new Date().getMonth(), 1, 0, 0, 0, 0)

  const competenciasCompletas = intervaloCompetencias(out1964, atual)
  const competenciasIntervalo = intervaloCompetencias(competenciaInicial, competenciaFinal)

  const tabelaVinculos: TTabela<TCelula> = dados.vinculos.map(vinculo => {
    const descricao = analisarDescricaoAtividade(vinculo.descricao)
    return [
      vinculo.nit,
      descricao[1],
      descricao[2],
      vinculo.dataInicial.nativo(),
      vinculo.dataFinal?.nativo()
    ]
  })

  const tabelaSalariosCompleta: TTabela<TCelula> = excluirSalarios
    ? []
    : competenciasCompletas.map(competencia => {
        return Array.prototype.concat(
          competencia,
          dados.vinculos.map(vinculo => {
            const remuneracao = Array.isArray(vinculo.remuneracoes)
              ? totalizarValoresContribuicoes(vinculo.remuneracoes, competencia)
              : null
            const contribuicao = Array.isArray(vinculo.contribuicoes)
              ? totalizarValoresContribuicoes(vinculo.contribuicoes, competencia)
              : null
            const contribuicaoAlt = Array.isArray(vinculo.contribuicoesAlt)
              ? totalizarValoresContribuicoes(vinculo.contribuicoesAlt, competencia)
              : null
            const contribuicaoGfip = Array.isArray(vinculo.contribuicoesGfip)
              ? totalizarValoresContribuicoes(vinculo.contribuicoesGfip, competencia)
              : null
            return (
              remuneracao || 0 || contribuicao || 0 || contribuicaoAlt || 0 || contribuicaoGfip || 0
            )
          })
        )
      })

  const tabelaSalariosIntervalo: TTabela<TCelula> = excluirSalarios
    ? []
    : competenciasIntervalo.map(competencia => {
        return Array.prototype.concat(
          competencia,
          dados.vinculos.map(vinculo => {
            const remuneracao = Array.isArray(vinculo.remuneracoes)
              ? totalizarValoresContribuicoes(vinculo.remuneracoes, competencia)
              : null
            const contribuicao = Array.isArray(vinculo.contribuicoes)
              ? totalizarValoresContribuicoes(vinculo.contribuicoes, competencia)
              : null
            const contribuicaoAlt = Array.isArray(vinculo.contribuicoesAlt)
              ? totalizarValoresContribuicoes(vinculo.contribuicoesAlt, competencia)
              : null
            const contribuicaoGfip = Array.isArray(vinculo.contribuicoesGfip)
              ? totalizarValoresContribuicoes(vinculo.contribuicoesGfip, competencia)
              : null
            return (
              remuneracao || 0 || contribuicao || 0 || contribuicaoAlt || 0 || contribuicaoGfip || 0
            )
          })
        )
      })

  const tabelaSalariosCompletaSemCompetencias = tabelaSalariosCompleta.map(linha => linha.slice(1))
  const tabelaSalariosIntervaloSemCompetencias = tabelaSalariosIntervalo.map(linha =>
    linha.slice(1)
  )

  const tabelaVinculosVertical: TTabela<TCelula> = [
    ['NIT:'],
    ['Empregador:'],
    ['Natureza do Vínculo:'],
    ['Data Inicial:'],
    ['Data Final:']
  ].map((titulo, indice) => {
    return Array.prototype.concat(
      titulo[0],
      tabelaVinculos.map(linha => linha[indice])
    )
  })
  const tabelaVinculosVerticalSemTitulos: TTabela<TCelula> = tabelaVinculosVertical.map(linha =>
    linha.slice(1)
  ) // retirando primeira coluna (de títulos)

  const periodos: TTabela<TCelula> = tabelaVinculos.map(linha => linha.slice(1)) // retirando somente a coluna no NIT
  const salariosCompleta: TTabela<TCelula> = excluirSalarios
    ? []
    : concatenarTabelas(
        tabelaVinculosVerticalSemTitulos,
        tabelaSalariosCompletaSemCompetencias,
        false,
        ''
      )
  const salariosIntervalo: TTabela<TCelula> = excluirSalarios
    ? []
    : concatenarTabelas(
        tabelaVinculosVerticalSemTitulos,
        tabelaSalariosIntervaloSemCompetencias,
        false,
        ''
      )
  const novaPagina: TTabela<TCelula> = concatenarTabelas(
    info,
    concatenarTabelas(
      tabelaVinculos.map(linha => linha.slice(1)),
      excluirSalarios
        ? []
        : concatenarTabelas(tabelaVinculosVertical, tabelaSalariosIntervalo, false, ''),
      false,
      ''
    ),
    false,
    ''
  )

  const resumo: TResumoCnis = [
    ['Segurado(a):', dados.segurado.nome],
    ['CPF:', dados.segurado.cpf],
    ['Dados:', `${dados.vinculos.length} vínculos encontrados`]
  ]

  return {
    resumo,
    salariosCompleta,
    salariosIntervalo,
    periodos,
    novaPagina,
    competenciaInicial,
    competenciaFinal
  }
}
