type TUnidade = {
  singular?: string;
  plural?: string;
};

export function porExtenso(n: number | string, moeda = false): string {
  if (typeof n === 'string') {
    if (!numerico(n.replace(/[.,]/g, ''))) return n;
    n = textoParaNumero(n);
  }
  if (n === 0) return `zero${moeda ? ' reais' : ''}`;

  const unidadesFracionarias = moeda === true ? { singular: 'centavo', plural: 'centavos' } : {};
  const unidadesInteiras = moeda === true ? { singular: 'real', plural: 'reais' } : {};

  const divisao = n.toFixed(2).toString().split('.');
  let parteFracionaria = divisao.length === 2 ? numeroPorExtenso(divisao[1], unidadesFracionarias) : '';
  if (parteFracionaria === 'zero') parteFracionaria = '';
  const parteInteira = numeroPorExtenso(divisao[0], unidadesInteiras);

  let conector;
  if (parteInteira.length === 0 || parteFracionaria.length === 0) {
    conector = '';
  } else {
    conector = moeda === true ? ' e ' : ' vírgula ';
  }

  return `${parteInteira.trim()}${conector}${parteFracionaria.trim()}`;
}

const TERMOS = {
  VINTENA: [
    'zero',
    'um',
    'dois',
    'três',
    'quatro',
    'cinco',
    'seis',
    'sete',
    'oito',
    'nove',
    'dez',
    'onze',
    'doze',
    'treze',
    'quatorze',
    'quinze',
    'dezesseis',
    'dezessete',
    'dezoito',
    'dezenove'
  ],
  DEZENAS: [null, 'dez', 'vinte', 'trinta', 'quarenta', 'cinquenta', 'sessenta', 'setenta', 'oitenta', 'noventa'],
  CENTENAS: [
    null,
    'cem',
    'duzentos',
    'trezentos',
    'quatrocentos',
    'quinhentos',
    'seiscentos',
    'setecentos',
    'oitocentos',
    'novecentos'
  ],
  GRANDEZAS_SINGULAR: [null, 'mil', 'milhão', 'bilhão', 'trilhão', 'quatrilhão'],
  GRANDEZAS_PLURAL: [null, 'mil', 'milhões', 'bilhões', 'trilhões', 'quatrilhões']
};

const numerico = (s: string) => !isNaN(parseFloat(s)) && isFinite(Number(s));
const textoParaNumero = (s: string): number => {
  const n = s
    .replace(/(R\$|\$)/, '')
    .replace(/\./g, '')
    .replace(/(,)(\d{1,})$/, '.$2');
  return Number(n);
};
const compactar = (item: unknown) => item !== null && item !== undefined;
const tirarZeros = (item: unknown) => item !== 'zero';

const definirEscala = (numero: number, grupo: number) =>
  numero === 1 ? TERMOS.GRANDEZAS_SINGULAR[grupo] : TERMOS.GRANDEZAS_PLURAL[grupo];

const incluirDe = function (s: string, singular = false) {
  const grandezas = singular ? TERMOS.GRANDEZAS_SINGULAR : TERMOS.GRANDEZAS_PLURAL;
  for (let i = 2; i < grandezas.length; i++) {
    const grandeza = grandezas[i];
    if (grandeza && s.endsWith(grandeza)) return true;
  }
  return false;
};

const ajustarVirgulas = (s: string) => {
  if (s.includes(', ') && s.lastIndexOf(', ') > s.lastIndexOf(' e ')) {
    const indice = s.lastIndexOf(', ');
    const primeiraParte = s.slice(0, indice);
    const segundaParte = s.slice(indice + 2);
    return `${primeiraParte} e ${segundaParte}`;
  }
  return s;
};

function numeroPorExtenso(valor: string | number, { singular = '', plural = '' }: TUnidade) {
  if (Number(valor) === 0) return 'zero';
  valor = valor.toString();

  const partes = [];
  let inicio = valor.length;
  while (inicio > 0) {
    const final = inicio;
    partes.push(valor.slice((inicio = Math.max(0, inicio - 3)), final));
  }

  let texto = partes
    .map((parte, indice) => {
      const n = Number(parte);
      if (n === 0) return null;

      const numeros = parte.split('').reverse().map(Number);

      const palavras = [];
      palavras.push(TERMOS.CENTENAS[numeros[2]]);
      if (numeros[1] === 1) {
        palavras.push(TERMOS.VINTENA[numeros[0] + 10]);
      } else {
        palavras.push(TERMOS.DEZENAS[numeros[1]]);
        palavras.push(TERMOS.VINTENA[numeros[0]]);
      }

      const escala = definirEscala(Number(parte), indice);
      const texto = palavras.filter(compactar).filter(tirarZeros).join(' e ');

      return `${texto}${escala ? ' ' + escala : ''}`;
    })
    .reverse()
    .filter(compactar)
    .join(', ');

  const unidades = Number(valor) > 1 ? plural : singular;
  const de = unidades && incluirDe(texto, texto.startsWith('um')) ? ' de' : '';

  texto = ajustarVirgulas(texto).replace('cem e', 'cento e');

  return `${texto}${de} ${unidades}`.trim();
}
