import { tipoString } from '../predicados';

/**
 * Remove todos os acentos de uma string.
 * Lança um erro se o parâmetro de entrada não é uma string.
 *
 * @param str
 */
export function semAcentuacao(str: string): string {
  if (!tipoString(str)) throw new Error('A função aceita somente strings!');
  return str
    .replace(/[áàãâä]/g, 'a')
    .replace(/[ÁÀÃÂÄ]/g, 'A')
    .replace(/[éèêë]/g, 'e')
    .replace(/[ÉÈÊË]/g, 'E')
    .replace(/[íìîï]/g, 'i')
    .replace(/[ÍÌÎÏ]/g, 'I')
    .replace(/[óòôõö]/g, 'o')
    .replace(/[ÓÒÔÖÕ]/g, 'O')
    .replace(/[úùüû]/g, 'u')
    .replace(/[ÚÙÜÛ]/g, 'U')
    .replace(/[ýÿ]/g, 'y')
    .replace(/[Ý]/g, 'Y')
    .replace(/[ñ]/g, 'n')
    .replace(/[Ñ]/g, 'N')
    .replace(/[ç]/g, 'c')
    .replace(/[Ç]/g, 'C');
}
