import type { IModulo, IResultadoAnalisador } from '../comuns'
import type { IDadosTc } from './tipos'
import { converterStringEmData } from '@cecalc/utils'
import gerarTabelas from './gerar-tabelas'
import gerarOpcoes from './gerar-opcoes'

const TempoContribuicao: IModulo = class Modulo {
  static nome = 'Períodos (Relação Simples)'

  static padrao = {
    periodos:
      /(\d{1,2}[./-]\d{1,2}[./-]\d{2,4})[-\s\t\n\raàteé]+(\d{1,2}[./-]\d{1,2}[./-]\d{2,4})([-\s\t\n\r]+\(.+?\))?/g,
    // grupos: 1) data inicial, 2) data final, 3) descrição
    dadosPeriodo:
      /(\d{1,2}[./-]\d{1,2}[./-]\d{2,4})[-\s\t\n\raàteé]+(\d{1,2}[./-]\d{1,2}[./-]\d{2,4})([-\s\t\n\r]+\(.+?\))?/
  }

  static reiniciarPadroes() {
    Modulo.padrao.periodos.lastIndex = 0
    Modulo.padrao.dadosPeriodo.lastIndex = 0
  }

  static testar(texto: string): boolean {
    Modulo.reiniciarPadroes()
    return Modulo.padrao.periodos.test(texto)
  }

  public analisar(texto: string): IResultadoAnalisador {
    Modulo.reiniciarPadroes()

    const dados = [] as IDadosTc[]

    texto.match(Modulo.padrao.periodos)?.forEach(periodo => {
      const dadosPeriodo = Modulo.padrao.dadosPeriodo.exec(periodo)
      if (Array.isArray(dadosPeriodo) && dadosPeriodo.length >= 3) {
        dados.push({
          dataInicial: converterStringEmData(dadosPeriodo[1]),
          dataFinal: converterStringEmData(dadosPeriodo[2]),
          descricao: dadosPeriodo.length > 3 ? dadosPeriodo[3].trim() : ''
        })
      }
    })

    const tabelas = gerarTabelas(dados)

    return gerarOpcoes(Modulo.nome, tabelas)
  }
}

export default TempoContribuicao
