import { tipoString } from '../predicados';

/**
 * Substitui todos os espaços, tabulações, quebras de linha e retornos de carro por espaços simples e apara os espaços iniciais e finais.
 * Lança um erro se o parâmetro de entrada não é uma string.
 *
 * @param str
 */
export function limparString(str: string): string {
  if (!tipoString(str)) throw new Error('A função aceita somente strings!');
  return str.replace(/\s+/g, ' ').trim();
}
