import { tipoString } from '../predicados';
import { validarParametrosDeData, validarParametrosDeHora } from '../validacao';

/**
 * Converte uma string em data.
 * O comportamento esperado é o seguinte:
 * - Se a string de entrada estiver no formato ISO-8061, ela é convertida utilizando o contrutor nativo do objeto `Date` após validação da string.
 * - Se a string de entrada estiver no formato ISO-8061 abreviado (i.e. no formato `YYYY-MM-DD`), ela é passada para o construtor nativo no formato `YYYY-MM-DDT00:00` (após validação) a fim de que seja aplicado o fuso local e não UTC.
 * - Nos demais casos, a string de entrada deve estar em algum dos formatos definidos na enumeração `FORMATOS_DATA`.
 *
 * Uso:
 * ```
 * console.log(converterStringEmData('06/01/2021'));
 * // Saída: Wed Jan 06 2021 00:00:00 GMT-0000 (UTC)
 * ```
 *
 * @param stringAConverter
 */
export function converterStringEmData(stringAConverter: string): Date {
  if (!tipoString(stringAConverter)) throw new Error('A função aceita somente strings!');
  if (PADROES.ISO_8601.test(stringAConverter) || PADROES.ISO_ABREVIADO.test(stringAConverter)) {
    return analisarISO(stringAConverter);
  }
  if (!stringValida(stringAConverter)) throw new Error('String inválida!');
  return analisarOutros(stringAConverter);
}

const PADROES = {
  ISO_8601: /^\d{4}-\d{2}-\d{2}\D\d{2}:\d{2}:\d{2}\.\d{3}Z$/, // '2016-01-01T02:00:00.000Z'
  ISO_ABREVIADO: /^\d{4}-\d{2}-\d{2}$/, // '2016-01-01'
  OUTROS: /^(\d{1,2}[-./])?\d{1,2}[-./]\d{2,4}$/
};

function stringValida(s: string): boolean {
  return PADROES.OUTROS.test(s);
}

function isValidIsoTime(s: string): boolean {
  const [horas, minutos, segundos, milisegundos] = s.split(/[:.]/g).map(Number);
  return validarParametrosDeHora(horas, minutos, segundos, milisegundos);
}

function validarISOAbreviada(s: string): boolean {
  const [ano, mes, dia] = s.split('-').map(Number);
  return validarParametrosDeData(ano, mes, dia);
}

function validarISO(s: string): boolean {
  if (PADROES.ISO_ABREVIADO.test(s)) {
    return validarISOAbreviada(s);
  }
  const [data, hora] = s.slice(0, -1).split('T');
  return validarISOAbreviada(data) && isValidIsoTime(hora);
}

function analisarISO(s: string): Date {
  if (!validarISO(s)) throw new Error('String inválida!');
  if (PADROES.ISO_ABREVIADO.test(s)) s += 'T00:00';
  return new Date(s);
}

function analisarOutros(s: string): Date {
  const fragmentos = s.split(/[-./]/g).map(Number);
  if (fragmentos.length === 2) fragmentos.unshift(1);

  // eslint-disable-next-line prefer-const
  let [dia, mes, ano] = fragmentos;

  ano = Number(('19' + ano).slice(-4));
  dia = dia ? dia : 1;

  if (!ano || !mes || !dia) {
    throw new Error('String inválida!');
  }
  if (!validarParametrosDeData(ano, mes, dia))
    throw new Error(`Data inválida ${ano}-${('00' + mes).slice(-2)}-${('00' + dia).slice(-2)}!`);

  return new Date(ano, mes - 1, dia);
}
