export function humanizar(s: string, formatoTitulo = false): string {
  return s
    .replace(/_/g, ' ')
    .replace(/\s+/g, ' ')
    .trim()
    .replace(/([a-z0-9])([A-Z])/g, '$1 $2')
    .toLowerCase()
    .replace(/^./, (str) => str.toUpperCase())
    .replace(/\s./g, (str) => (formatoTitulo ? str.toUpperCase() : str))
    .split(' ')
    .map((str) => (/^(e|de|d[ao]s?|[àao]s?|um[a]?|uns|umas|com)$/i.test(str) ? str.toLowerCase() : str))
    .join(' ');
}
