import type { IResultadoAnalisador } from '../comuns'
import type { IDadosHiscre, ITabelasHiscre } from './tipos'

export default function gerarOpcoes(
  nome: string,
  tabelas: ITabelasHiscre,
  dados: IDadosHiscre
): IResultadoAnalisador {
  return {
    nome: nome,
    resumo: tabelas.resumo,
    opcoes: [
      {
        nome: 'Benefício originário',
        parametros: [
          {
            intervalo: 'NBOriginario',
            dados: [[dados.beneficio.nb]]
          },
          {
            intervalo: 'EspecieOriginario',
            dados: [[dados.beneficio.especie]]
          },
          {
            intervalo: 'DIBOriginario',
            dados: [[dados.beneficio.dib]]
          },
          {
            intervalo: 'DCBOriginario',
            dados: [[dados.beneficio.dcb]]
          }
        ]
      },
      {
        nome: 'Benefício derivado',
        parametros: [
          {
            intervalo: 'NBDerivado',
            dados: [[dados.beneficio.nb]]
          },
          {
            intervalo: 'EspecieDerivado',
            dados: [[dados.beneficio.especie]]
          },
          {
            intervalo: 'DIBDerivado',
            dados: [[dados.beneficio.dib]]
          },
          {
            intervalo: 'DCBDerivado',
            dados: [[dados.beneficio.dcb]]
          }
        ]
      },
      {
        nome: 'Dados gerais do benefício',
        parametros: [
          {
            intervalo: 'NB',
            dados: [[dados.beneficio.nb]]
          },
          {
            intervalo: 'Especie',
            dados: [[dados.beneficio.especie]]
          },
          {
            intervalo: 'DIB',
            dados: [[dados.beneficio.dib]]
          },
          {
            intervalo: 'DCB',
            dados: [[dados.beneficio.dcb]]
          }
        ]
      },
      {
        nome: 'Descontos valores recebidos',
        parametros: [
          {
            intervalo: 'DescontosNB',
            dados: tabelas.pagamentos,
            competenciaInicial: tabelas.competenciaInicial,
            competenciaFinal: tabelas.competenciaFinal
          }
        ]
      },
      {
        nome: 'Nova Página',
        parametros: [
          {
            intervalo: '',
            dados: tabelas.novaPagina,
            competenciaInicial: tabelas.competenciaInicial,
            competenciaFinal: tabelas.competenciaFinal
          }
        ]
      }
    ]
  }
}
