import { tipoNumero } from '../predicados';

/**
 * Validates hours, minutes, seconds and milliseconds, checking if all parameters are numbers and are within the correct range.
 *
 * @param hora
 * @param minutos
 * @param segundos
 * @param milisegundos
 */
export function validarParametrosDeHora(
  hora: number,
  minutos: number,
  segundos: number,
  milisegundos: number
): boolean {
  if (!tipoNumero(hora) || !tipoNumero(minutos) || !tipoNumero(segundos) || !tipoNumero(milisegundos)) return false;
  if (
    hora < 0 ||
    hora > 23 ||
    minutos < 0 ||
    minutos > 59 ||
    segundos < 0 ||
    segundos > 59 ||
    milisegundos < 0 ||
    milisegundos > 999
  )
    return false;
  return true;
}
